const playlist = {
  template: document.querySelector("#ytb-template"),
  targetContainer: document.querySelector('.swiper-wrapper'),

  init(){
    this.getData()
  },

  getData() {
    const baseUrl = 'https://www.googleapis.com/youtube/v3/playlistItems';
    const part = 'snippet';
    const apiKey = import.meta.env.VITE_API_KEY;
    const playlistId = import.meta.env.VITE_PLAYLIST_ID;
    const maxResults = 10;

    const url = `${baseUrl}?part=${part}&playlistId=${playlistId}&maxResults=${maxResults}&key=${apiKey}`;
    console.log('getData', import.meta.env.VITE_API_KEY);


    fetch(url)
    .then(response => {
      if (!response.ok) {
        throw new Error('Network response was not ok');
      }
      return response.json();
    })
    .then(data => {
      let list = data.items;
      console.log(list);
      // for (let i = 0; i < list.length; i++) {
      //   let clone = iframePlayer.cloneNode(true);
      //   clone.classList.remove('hide');
      //   clone.src = 'https://www.youtube.com/embed/' + list[i].snippet.resourceId.videoId;
      //   document.getElementById('videos').appendChild(clone);
      // }
    })
    .catch(error => {
      console.error('There was a problem with the fetch operation:', error);
    });

  }

}
export default playlist
